import {Greeting} from './interfaces';

export default class Greeter {
  greeting: Greeting;

  constructor(greeting: Greeting) {
    this.greeting = greeting;
  }

  greet() {
    const {
      greeting,
      greetingTarget,
      version,
    } = this.greeting;

    return `Greeter version ${version} says: ${greeting} ${greetingTarget}!`;
  }
}
